import React from "react";
import Signup from "./components/signup/Signup"
import Login from "./components/login/login"
import Dashboard from "./components/Dashboard/Dashboard"
import {

  Switch,
  Route,
  Link

} from "react-router-dom";

function App() {

  return (
    <>
      <Switch>

        <Route path="/login">
          <Login />
        </Route>

        <Route path="/profile">
          <Dashboard/>
        </Route>

        <Route exact path="/">
          <Signup/>
        </Route>

      </Switch>
    </>




  );
}

export default App;
